import React, { useState } from "react";
import QuizPageHeader from "./QuizPageHeader.js";
import { useLocation } from "react-router";
import styled from "styled-components";
import { useTheme } from "../../context/themeProvider.js";

const QuizPage = () => {
  const location = useLocation();
  const bookId = location.state ? location.state.props : null; // bookId 
  const [themeMode] = useTheme();

  
  //   
  const questionsBook38 = [
    {
      id: 1,
      question: "    ?",
      options: [
        "    ",
        "     ",
        "      ",
        "    ",
      ],
      answer: 2,
    },
    {
      id: 2,
      question: "'' ?",
      options: [
        " ,    ",
        "   ",
        "     ",
        "   DNA ",
      ],
      answer: 0,
    },
    {
      id: 3,
      question: "   ?",
      options: [
        "      ",
        "     ",
        "       ",
        "   ",
      ],
      answer: 2,
    },
    {
      id: 4,
      question: "  ?",
      options: [
        "     ",
        "    ",
        "    ",
        "     ",
      ],
      answer: 1,
    },
    {
      id: 5,
      question: "  '  ' ?",
      options: [
        "    ",
        "     ",
        "       ",
        "    ",
      ],
      answer: 2,
    },
  ];
  
  const questionsBook49 = [
    {
      id: 1,
      question: "      ?",
      options: [
        "    ",
        "    ",
        "    ",
        "     ",
      ],
      answer: 0,
    },
    {
      id: 2,
      question: "         ?",
      options: [
        "   ",
        "     ",
        "   ",
        "    ",
      ],
      answer: 1,
    },
    {
      id: 3,
      question: "      ?",
      options: [
        "      ",
        "   ",
        "       ",
        "     ",
      ],
      answer: 2,
    },
    {
      id: 4,
      question: "      ?",
      options: [
        "   ",
        "    ",
        "    ",
        "     ",
      ],
      answer: 1,
    },
    {
      id: 5,
      question: "     ,     ?",
      options: [
        "        ",
        "         ",
        "         ",
        "       ",
      ],
      answer: 0,
    },
  ];
  
  const questions = bookId === 38 ? questionsBook38 : bookId === 49 ? questionsBook49 : [];
  
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [isCorrect, setIsCorrect] = useState(null);
  const [score, setScore] = useState(0); //  
  const [wrongAnswers, setWrongAnswers] = useState(0); //  

  const currentQuestion = questions[currentQuestionIndex];

  const handleAnswerSelect = (index) => {
    setSelectedAnswer(index);
    const correct = index === currentQuestion.answer;
    setIsCorrect(correct);
    if (correct) {
      setScore((prev) => prev + 1);
    } else {
      setWrongAnswers((prev) => prev + 1);
    }
  };

  const handleNextQuestion = () => {
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex((prev) => prev + 1);
      setSelectedAnswer(null);
      setIsCorrect(null);
    }
  };

  return (
    <>
      <QuizPageHeader bookId={bookId} />
      <TotalBox>
        <ReadBox theme={themeMode}>
          <ImgStyle src={`/BookImg/${bookId}.png`} />
        </ReadBox>
        <ReadBox1 theme={themeMode}>
          <QnaStyle src="/QnA/QnA.png" />
          <ProgressText theme={themeMode}>
            {` ${currentQuestionIndex + 1} ,  ${questions.length}  ${questions.length - (currentQuestionIndex + 1)} `}
          </ProgressText>
          <QnaText theme={themeMode}>
            <div>Q. {currentQuestion.question}</div>
            {currentQuestion.options.map((option, index) => (
              <OptionButton
                key={index}
                theme={themeMode}
                onClick={() => handleAnswerSelect(index)}
                selected={selectedAnswer === index}
              >
                {option}
              </OptionButton>
            ))}
            {selectedAnswer !== null && (
              <ResultText theme={themeMode}>
                {isCorrect ? "!" : `! : ${currentQuestion.options[currentQuestion.answer]}`}
              </ResultText>
            )}
            {selectedAnswer !== null && currentQuestionIndex < questions.length - 1 && (
              <NextButton theme={themeMode} onClick={handleNextQuestion}>
                 
              </NextButton>
            )}
            {currentQuestionIndex === questions.length - 1 && selectedAnswer !== null && (
              <ResultText theme={themeMode}>
                 ! <br />
                : {score}, : {wrongAnswers}
              </ResultText>
            )}
          </QnaText>
        </ReadBox1>
      </TotalBox>
    </>
  );
};

export default QuizPage;

const TotalBox = styled.div`
  display: flex;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  gap: 15px;
`;

const ReadBox = styled.div`
  width: 565px;
  height: 700px;
  background-color: ${(props) => (props.theme === "light" ? "#antiquewhite" : "#030314")};
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ImgStyle = styled.img`
  width: 100%;
  height: 100%;
`;

const ReadBox1 = styled.div`
  width: 565px;
  height: 700px;
  background-color: ${(props) => (props.theme === "light" ? "#antiquewhite" : "#030314")};
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
  position: relative;
`;

const QnaStyle = styled.img`
  width: 600px;
  height: 700px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
`;

const QnaText = styled.div`
  width: 100%;
  color: ${(props) => (props.theme === "light" ? "#000000" : "#ffffff")};
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ProgressText = styled.div`
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: bold;
  color: ${(props) => (props.theme === "light" ? "#000000" : "#ffffff")};
`;

const OptionButton = styled.button`
  padding: 10px 20px;
  background-color: ${(props) => (props.selected ? "#D3D3D3" : "#FFFFFF")};
  border: 1px solid ${(props) => (props.theme === "light" ? "#000000" : "#ffffff")};
  color: ${(props) => (props.theme === "light" ? "#000000" : "#ffffff")};
  cursor: pointer;
`;

const ResultText = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
`;

const NextButton = styled.button`
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
`;
