import styled from "styled-components";
import React, { useEffect, useState } from "react";
import axios from 'axios';
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
import { icons } from '../assets/image.js';

const Booklist = () => {
    
    const [bookdata, setdata] = useState([]);
    const [bookPage, setBookPage] = useState(1);
    const [aiBookPage, setAiBookPage] = useState(10);
    const [bookAidata, setAidata] = useState([]);
    // imageUrlArray AiImageUrlArray  
    const [imageUrlArray, setImageUrlArray] = useState([]);
    const [AiImageUrlArray, setAiImageUrlArray] = useState([]);
    const [userId,setUserId] = useState('');
    const serverIP = process.env.REACT_APP_SERVER_URL;
    const [userInfo, setUserInfo] =useState(null);
    const [likedBooks, setLikedBooks] = useState([]); // :   
    const [bookId, setBookId] = useState("");
    const [userpage,setUserPage] = useState(0);
    const [bookLast,setBookLast] = useState(0);


    
    const navigate = useNavigate();

     
    //    
    useEffect(()=>{
        const fetchUserInfo = async () => {
           const token = localStorage.getItem("token");
           console.log("dasd");
           try {
               const response = await axios.get(`${serverIP}/user-info`, {
                   headers: {
                       Authorization: `Bearer ${token}`
                   }
               });
               setUserInfo(response.data);
               setUserId(response.data.userId);
               console.log('   :',response.data);
               console.log(' id ',response.data.userId);
           } catch (error) {
               console.error("Error fetching user info:", error);
               //   (:   )
           }
       };
  
       fetchUserInfo();
   }, []);

   useEffect(() => {
    if (userId) {
        axios.get(`${serverIP}/book/${userId}/findLike`)
            .then((res) => {
                setLikedBooks(res.data.map(item => item.bookId));
                // window.location.reload();
            })
            .catch((error) => { 
                console.log(error); 
                handleClick();
            });
    }
}, [userId]); // userId  


    useEffect(() => {
        // bookdata   imageUrlArray 
        setImageUrlArray(bookdata.map(item => `/BookImg/${item.bookid}.png`));

        //  ()   ,     
        if (userId) {
            axios.get(`${serverIP}/book/${userId}/findLike`)
                .then((res) => {
                    setLikedBooks(res.data.map(item => item.bookId));
                    
                })
                .catch((error) => { 
                    console.log(error) 
                    handleClick()
                });
        }
    }, [bookdata]);

    useEffect(() => {
        // bookAidata   AiImageUrlArray 
        setAiImageUrlArray(bookAidata.map(item => `/BookImg/${item.bookid}.png`));
    }, [bookAidata]);   

    useEffect(() => {
        const serverIP = process.env.REACT_APP_SERVER_URL;

        axios.get(`${serverIP}/book/find/${bookPage}`)
            .then((res) => {
                console.log(res);
                const receivedData = res.data;
                const newDataArray = receivedData.map(item => ({
                    bookid: item.bookid,
                    imageUrl: item.bookid,
                    page: item.page,
                    author: item.author,
                    publisher: item.publisher,
                    story: item.story,
                    price: item.price,
                    theme: item.theme,
                    title: item.title
                }));
                setdata(newDataArray);
            })
            .catch((error) => { console.log(error) });
    }, [bookPage]);

    useEffect(() => {
        const serverIP = process.env.REACT_APP_SERVER_URL;
        axios.get(`${serverIP}/book/findAi/${aiBookPage}`)
            .then((resAi) => {
                console.log(resAi);
                console.log("asdasdas");
                const receivedDataAi = resAi.data;
                const newDataArrayAi = receivedDataAi.map(item2 => ({
                    bookid: item2.bookid,
                    page: item2.page,
                    author: item2.author,
                    publisher: item2.publisher,
                    story: item2.story,
                    price: item2.price,
                    theme: item2.theme,
                    title: item2.title
                }));
                setAidata(newDataArrayAi);
            })
            .catch((error) => { console.log(error) });
    }, [aiBookPage]);
    // 
    const handleNextPage = () => {
        setBookPage(prevPage => prevPage + 1);
    };
    // 
    const handlePreviousPage = () => {
        if (bookPage > 1) {
            setBookPage(prevPage => prevPage - 1);
        }
    };
    //ai  
    const handleNextPage2 = () => {
        setAiBookPage(prevPage => prevPage + 1);
    };
    //ai  
    const handlePreviousPage2 = () => {
        if (aiBookPage > 1) {
            setAiBookPage(aiBookPage => aiBookPage - 1);
        }
    };
    const fetchPage = async (bookId) => {
        try {
            console.log(bookId+" ");
            const response = await axios.get(`${serverIP}/book/bookmark/page/${userInfo.nickname}/${bookId}`);
            console.log(" :", response.data);
            return response.data;
        } catch (error) {
            console.error("  :", error);
        }
    };
    //  
    const handleReadButtonClick = async (bookId) => {
        const userPage = await fetchPage(bookId);
        const selectedBook = bookdata.find(book => book.bookid === bookId);
        
        if (selectedBook) {
            navigate("/read", { 
                state: { 
                    props: bookId,
                    lastpage: selectedBook.page,
                    userpage: userPage,
                    thema: selectedBook.theme,
                    title: selectedBook.title,
                }
            });
        }
    };
    const handleReadButtonClickAi = async (bookId) => {
        const userPage = await fetchPage(bookId);
        const selectedBook = bookAidata.find(book => book.bookid === bookId);
        
        console.log(selectedBook.page,userPage+"  ")

        if (selectedBook) {
            navigate("/read", { 
                state: { 
                    props: bookId,
                    lastpage: selectedBook.page,
                    userpage: userPage,
                    thema: selectedBook.theme,
                    title: selectedBook.title,
                }
            });
        }
    };

    //   
    const [liked, setLiked] = useState(false);

    //   
    const handleClick = (bookId) => {
        if (userId == null) return;
    
        //   
        const updatedLikedBooks = likedBooks.includes(bookId)
            ? likedBooks.filter(id => id !== bookId) //     
            : [...likedBooks, bookId]; //   
    
        setLikedBooks(updatedLikedBooks);
    
        const data = {
            bookId: Number(bookId),
            userId: String(userId)
        };
    
        //    
        axios.post(`${serverIP}/user/likeBook`, data, { headers: { "Content-Type": 'application/json' } })
            .catch((e) => {
                console.error('  /   . error code:', e);
            });
    };



    return (
        <div>
            <MainBox>
                <div style={{ fontWeight: 'bold', fontSize: '30px', float: 'left' }}>
                    AI  
                </div>
                <SecondBox>
                    <ButtonContainer>
                        <ButtonPrev onClick={handlePreviousPage2}></ButtonPrev>
                    </ButtonContainer>
                    <div style={{marginTop:"10px"}}>
                    <BookBox>
                        {bookAidata.map((item2, index) => (
                            <Book key={index}>
                                <Link to={`/details/${item2.title}`}>
                                    <BookImg src={AiImageUrlArray[index]} alt="Book Cover" />
                                </Link>
                                <div style={{ height: '20px' }}></div>
                                <BookTitle>{item2.title}</BookTitle>
                                <BookWriter>{item2.author}</BookWriter>
                                <BookTranslator>{item2.publisher}</BookTranslator>
                                <BookPriceLike>
                                    <BookPrice>{item2.price.toLocaleString('ko-KR')}</BookPrice>
                                    <BookLike
                                        src={likedBooks.includes(item2.bookid) ? icons.like : icons.like_before} //  : likedBooks    
                                        alt="bookmark"
                                        onClick={() => handleClick(item2.bookid)} //    
                                        style={{ cursor: 'pointer' }}
                                    />
                                </BookPriceLike>
                                
                                <div style={{ height: '20px' }}></div>
                                    <BookReadBox onClick={() => handleReadButtonClickAi(item2.bookid)}> 
                                    <BookReadButton  >
                                          
                                    </BookReadButton>
                                    </BookReadBox>
                            </Book>
                        ))}
                    </BookBox>
                    </div>
                    <ButtonContainer>
                        <ButtonNext  onClick={handleNextPage2}></ButtonNext>
                    </ButtonContainer>
                </SecondBox>
                <div style={{ height: "20px", marginTop: "100px" }}></div>
                <div style={{ fontWeight: 'bold', fontSize: '30px', float: 'left'}}>
                     
                </div>
                <SecondBox>
                    <ButtonContainer>
                        <ButtonPrev onClick={handlePreviousPage}></ButtonPrev>
                    </ButtonContainer>
                    <div>
                        <BookBox>
                            {bookdata.map((item, index) => (
                                <Book key={index}>
                                    <Link to={`/details/${item.title}`}>
                                        <BookImg src={imageUrlArray[index]}/>
                                    </Link>
                                    <div style={{ height: '20px' }}></div>
                                    <BookTitle>{item.title}</BookTitle>
                                    <BookWriter>{item.author}</BookWriter>
                                    <BookTranslator>{item.publisher}</BookTranslator>
                                    <BookPriceLike>
                                        <BookPrice>{item.price.toLocaleString('ko-KR')}</BookPrice>
                                        <BookLike
                                        src={likedBooks.includes(item.bookid) ? icons.like : icons.like_before} //  : likedBooks    
                                        alt="bookmark"
                                        onClick={() => handleClick(item.bookid)} //    
                                        style={{ cursor: 'pointer' }}
                                    />
                                    </BookPriceLike>
                                    <div style={{ height: '20px' }}></div>
                                        <BookReadBox onClick={() => handleReadButtonClick(item.bookid)}>
                                            <BookReadButton >
                                                   
                                            </BookReadButton>
                                        </BookReadBox>
                                </Book>
                            ))}
                        </BookBox>
                    </div>
                    <ButtonContainer>
                        <ButtonNext onClick={handleNextPage}></ButtonNext>
                    </ButtonContainer>
                </SecondBox>
            </MainBox>
        </div>
    )
}

const MainBox = styled.div`
    width: 1350px;
    border: 2px solid;
    border-color: ${({ theme }) => theme.bgColor || '#ffffff'};
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
`;

const SecondBox = styled.div`
    display: flex;
    flex-direction: row;
    margin-Top:40px;
    align-items: center;


`;

const ButtonContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;


`;

const BookBox = styled.div`
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px;
    margin-top: 30px;
    margin-left: 0px;

`;

const Book = styled.div`
    width: 282px;
    height: 580px;
    margin-top: 30px;
    margin: 0px;
    @media (max-width: 768px) {
        width: 45%;
    }
`;

const BookImg = styled.img`
    width: 100%;
    height: 400px;

    margin: 0 auto;
`;

const BookTitle = styled.div`
    height: 30px;
    vertical-align: middle;
    font-weight: bold;
`;

const BookWriter = styled.div`
    margin-top:20px;
    font-size: 12px;
`;

const BookTranslator = styled.div`
    font-size: 12px;
`;

const BookPrice = styled.div`
    font-size: 13px;
    font-weight: bold;
`;

const BookPriceLike = styled.div`
    display: flex;
    justify-content: space-between;
`;

const BookLike = styled.img`
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const BookReadBox = styled.div`
    width: 100%;
    height: 20px; 
    background-color: #937DC2;
    border-radius: 2px;
    display: flex; 
    justify-content: center; 
    align-items: center; 
    padding: 10px 0; 
    cursor: pointer;
`;

const BookReadButton = styled.button`
    background-color: transparent;
    color: white; 
    display: block; 
    border: 0px ;
    cursor: pointer;
`;

const ButtonNext = styled.button`
    width: 30px;
    height: 30px;
    font-size: 40px;
    border-radius: 30px;
    border : none;
    margin: 0px;
    cursor: pointer;
    background-color:transparent;
    color: gray;


`;

const AiBookBox = styled.div``;

const TitleBox = styled.div`
    float: left;
    margin-right: 20px;
`;

const ButtonPrev = styled(ButtonNext)`
    float: left;
    margin: 10px;
`;

export default Booklist;
